import { useEffect } from "react";
import { withFirebaseProvider, useFirebase } from "components/FirebaseProvider";

import { navigate } from "gatsby";
import DashboardLayout from "components/layouts/DashboardLayout";

const LoginPage = () => {
  const { firebase, isLoading, user } = useFirebase();

  useEffect(() => {
    if (!firebase) return;
    if (!isLoading) {
      if (user) navigate("/dashboard/schedule");
    }
  }, [firebase, isLoading, user]);

  return (
    <DashboardLayout>ДОБРО ПОДАЛОВАТЬ В HIPSTA CODING SCHOOl</DashboardLayout>
  );
};

export default withFirebaseProvider(LoginPage);
